import React, { Component } from 'react';
import parse from 'html-react-parser';


import { StaticQuery, graphql } from "gatsby"

export default function Footer() {
  return (
    <StaticQuery
      query={graphql`
      query Footer {
        allWpPost(filter: {slug: {eq: "footer-rera"}}) {
          nodes {
            content
          }
        }
        footer:allWpPost(filter: {slug: {eq: "footer"}}) {
          nodes {
            content
          }
        }
      }
      `}
      render={data => (
        
        <footer>
          <div class="container">
            <p class="rera-container"> <b> RERA Registration Number :</b> </p>
            {parse(data.allWpPost.nodes[0].content)}
            <div class="leagal"> <a href="./terms">Terms Of Use</a> | <a href="./policy">Privacy Policy</a> | <a href="./disclaimer">Disclaimer</a> </div>
            <p class="disclaimer">Thank you for visiting our website. We are currently in the process of revising our website in accordance under RERA.We are in the process of updating the website. The content provided on this website is for informational purposes only, and
              should not be construed as legal advice on any subject matter. Without any limitation or qualification, users hereby agree with this Disclaimer, when accessing or using this website.</p>
              {parse(data.footer.nodes[0].content)}
          </div>
        </footer>

      )}
    />
  )
}