import React, { Component } from 'react'
import { StaticQuery, graphql } from "gatsby"
import LocationMapContact from "../images/location.svg"
import Globe from "../images/globe.svg"
import Email from "../images/email.svg"
import Phone from "../images/phone.svg"
import $ from "jquery";

export default function Contact() {
  const fnSendMeDetail = (form,pid,pname) => {
    var name = $("#" + form + " input[name=name]").val();
    var email = $("#" + form + " input[name=email]").val();
    var mobile_no = $("#" + form + " input[name=mobile]").val();

    var email_pattern = /^\w+([-+.'][^\s]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/;
    var mobile_length = mobile_no.length;

    if (name == "") {
      $("#" + form + " .form_error").html("Please enter name");
      return false;
    } else {
      $("#" + form + " .form_error").html("");
    }

    if (email != "") {
      if (!email_pattern.test(email)) {
        $("#" + form + " .form_error").html("Please enter valid email");
        return false;
      } else {
        $("#" + form + " .form_error").html("");
      }
    }

    if (mobile_no == "") {
      $("#" + form + " .form_error").html("Please enter mobile no.");
      return false;
    } else if (mobile_length != 10) {
      $("#" + form + " .form_error").html("Please enter valid mobile no.");
      return false;
    } else {
      $("#" + form + " .form_error").html("");
    }

    $('#' + form + ' button').prop('disabled', true);

    var lead = {
      name: name,
      email: email,
      mobile: mobile_no,
      project: pname,
      project_id: pid
    };

    $.ajax({
      type: "post",
      url: 'https://mls-crm.manishalifespaces.com/api/add_lead',
      data: lead,
      success: function (data, status, jqXHR) {
        window.location.href = 'thanks';
      },
      error: function (jqXHR, status) {
        $('#' + form + ' button').prop('disabled', false);
      }
    });
  }
  return (
    <StaticQuery
      query={graphql`
      query ContactUs {
        address: allWpPost(filter: {slug: {eq: "address"}}) {
          edges {
            node {
                id
              content
            }
          }
        }
        website: allWpPost(filter: {slug: {eq: "website"}}) {
          edges {
            node {
              id
              content
            }
          }
        }
        emailId: allWpPost(filter: {slug: {eq: "email-id"}}) {
          edges {
            node {
              id
              content
            }
          }
        }
        primaryContact: allWpPost(filter: {slug: {eq: "primary-contact"}}) {
          edges {
            node {
              id
              content
            }
          }
        }
        allWpPost(filter: {uri: {}, slug: {eq: "logo"}}) {
          nodes {
            slug
            content
            featuredImage {
              node {
                sourceUrl
              }
            }
          }
        }
        ProjectId:allWpPost(filter: {uri: {}, slug: {eq: "project-id"}}) {
          nodes {
            content
          }
        }
        ProjectName:allWpPost(filter: {uri: {}, slug: {eq: "project-name"}}) {
          nodes {
            content
          }
        }
      }
      
      `}
      render={data => (

        <section id="contact-sales" class="bg">
          <div class="container">
            <div class="box"></div>
            <div class="header-title">
              <h2 class="text-center">Contact Sales</h2>
              <div class="underline"></div>
            </div>
            <div class="row">
              <div class="col-md-5">
                <div class="content">
                  <div class="info">
                    <div class="text-center mb-4"> <a href="./"> <img style={{ width: "16rem" }} src={data.allWpPost.nodes[0].featuredImage.node.sourceUrl} /> </a> </div>
                    <div class="text-center mb-4">  </div>
                    <div class="d-flex mb-3"> <img style={{ width: "30px" }} src={LocationMapContact} />
                      <p class="ps-3 m-0"><div dangerouslySetInnerHTML={{ __html: data.address.edges[0].node.content }} /></p>
                    </div>
                    <div class="d-flex mb-3"><img style={{ width: "30px" }} src={Globe} />
                      <p class="ps-3 m-0"><a href=""><div dangerouslySetInnerHTML={{ __html: data.website.edges[0].node.content }} /></a></p>
                    </div>
                    <div class="d-flex mb-3"> <img style={{ width: "30px" }} src={Email} />
                      <p class="ps-3 m-0"><a href=""><div dangerouslySetInnerHTML={{ __html: data.emailId.edges[0].node.content }} /></a></p>
                    </div>
                    <div class="d-flex mb-3"> <img style={{ width: "30px" }} src={Phone} />
                      <p class="ps-3 m-0"><a href=""><div dangerouslySetInnerHTML={{ __html: data.primaryContact.edges[0].node.content }} /></a></p>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-md-7">
                <div class="content">
                  <div class="visible-xs">
                    <h3 class="text-center">Fill your contact details</h3>
                  </div>
                  <form id="contactForm" class="pform">
                    <div class="mb-3">
                      <input type="text" class="form-control" name="name" placeholder="Name" />
                    </div>
                    <div class="mb-3"> <input type="email" class="form-control" name="email" placeholder="Email" /> </div>
                    <div class="mb-3"> <input type="number" class="form-control mobile" name="mobile" placeholder="Mobile No." /> </div>
                    {/* <div class="form-check mb-3"> <input class="form-check-input" type="checkbox" value="" id="contact-check" /> <label class="form-check-label" for="contact-check"> Yes, I want quick call back </label> </div> */}
                    <p class="text-danger form_error"></p><button type="button" class="btn btn-primary shadow-lg w-100" onClick={() => fnSendMeDetail("contactForm",data.ProjectId.nodes[0].content.replace(/<\/?[^>]+(>|$)/g, ""),data.ProjectName.nodes[0].content.replace(/<\/?[^>]+(>|$)/g, ""))}>SEND ME DETAILS</button>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </section>
      )}
    />
  )
}